import { changeLineWidth } from './progress-bar';
const $tab = $('.js-tab');
const $tabContent = $('.js-tab-content');

const $tabInner = $('.js-tab-inner');
const $tabContentInner = $('.js-tab-content-inner');

$tab.on('click', toggleTabContent);
$tabInner.on('click', toggleInnerTabContent);

function toggleTabContent() {
    const tabToShow = $(this).data('tab');
    $tab.removeClass('is-active');
    $tabContent.removeClass('is-active');
    $(this).addClass('is-active');

    $(`#${tabToShow}`).addClass('is-active');
    changeLineWidth($(this));
}

function toggleInnerTabContent() {
    const tabToShow = $(this).data('tab');
    $tabInner.removeClass('is-active');
    $tabContentInner.removeClass('is-active');
    $(this).addClass('is-active');
    $(`#${tabToShow}`).addClass('is-active');
}
