const $proggressLine = $('.js-progress-line');
const $activeTab = $('.js-tab.is-active');
const $currentTabIndex = $('.js-current-tab-index');
const $lastTabIndex = $('.js-last-tab-index');
changeLineWidth( $activeTab );

function changeLineWidth($activeTab) {
  let $this = $activeTab;
  let currentIndex = $this.data('tab');
  let lastIndex = $this.data('tab-end');
  let progressLineWidthPercents = currentIndex / lastIndex;

  $currentTabIndex.html(currentIndex);
  $lastTabIndex.html(lastIndex);

  $proggressLine.css('transform', `scaleX(${progressLineWidthPercents})`);
}

export { changeLineWidth };