const $burger = $('.js-burger');
const $body = $('body');
const $menu = $('.js-menu');
const $languageToggler = $('.language-toggler');

$burger.on('click', function() {
    $(this).toggleClass('is-active');
    $menu.toggleClass('is-active');
    $body.toggleClass('is-static');
    $languageToggler.toggleClass('is-active');
});